<template>
	<view class="body">
		<view id="top-bg">
			<view class="info-all">
				<view id="touxiang" class="cu-avatar xl round" :style="{backgroundImage: 'url(' + portrait +')'}"></view>
				<view class="info-details">
					<h1 class="text-white" id="en_font">Welcome!</h1>
					<text class="text-white" style="font-size: small;" id="ch_font">工号：{{num}}</text>
					<text class="text-white" style="font-size: small;" id="ch_font">姓名：{{usrName}}</text>

				</view>
			</view>
			<view class="application">
				<view class="cu-list grid no-border col-3" id="grid1">
					<view class="cu-item">
						<navigator url="index">
							<view class="cuIcon-search text-green"></view>
							<text>打卡查询</text>
						</navigator>
					</view>
					<view class="cu-item">
						<navigator url="DownPullRefresh">
							<view class="cuIcon-taxi text-black"></view>
							<text>交通</text>
						</navigator>
					</view>
					<view class="cu-item">
						<view class="cuIcon-favor text-orange"></view>
						<text>收藏</text>
					</view>
					<view class="cu-item">
						<view class="cuIcon-mail text-cyan"></view>
						<text>邮件</text>
					</view>
					<view class="cu-item">
						<view class="cuIcon-edit text-pink"></view>
						<text>调查问卷</text>
					</view>
					<view class="cu-item">
						<view class="cuIcon-radiobox text-brown"></view>
						<text>其他</text>
					</view>

				</view>
			</view>
			<view class="uni-margin-wrap">
				<!-- <swiper class="swiper" circular :indicator-dots="indicatorDots" :autoplay="autoplay" :interval="interval" :duration="duration">
					<swiper-item class="swiper-img" v-for="(item,index) in imgList" :key="index">
						<view class="swiper-item">
							<image :src="item.url"></image>
						</view>
					</swiper-item>
				</swiper>-->
				<!-- <br> 
				<swiper class="card-swiper" :class="dotStyle?'square-dot':'round-dot'" :indicator-dots="true" :circular="true"
				 :autoplay="true" :interval="interval" :duration="duration" @change="cardSwiper" indicator-color="#8799a3"
				 indicator-active-color="#0081ff">
					<swiper-item v-for="(item,index) in imgList" :key="index" :class="cardCur==index?'cur':''">
						<view class="swiper-item">
							<image :src="item.url" mode="aspectFill"></image>
						</view>
					</swiper-item>
				</swiper> -->
			</view>
			<br>
			<view class="cu-list menu" :class="[menuBorder?'sm-border':'',menuCard?'card-menu margin-top':'']">
				<view class="cu-item " :class="menuArrow?'arrow':''">
					<button class="cu-btn content " open-type="contact">
						<navigator url="index">
							<text class="cuIcon-circlefill text-grey"></text>
							<text class="text-grey">图标 + 标题</text>
						</navigator>
					</button>
				</view>
				<view class="cu-list menu" :class="[menuBorder?'sm-border':'',menuCard?'card-menu margin-top':'']">
					<view class="cu-item" :class="menuArrow?'arrow':''">
						<button class="cu-btn content" open-type="contact">
							<navigator url="index">
								<text class="cuIcon-ellipse text-blue"></text>
								<text class="text-grey">图片 + 标题</text>
							</navigator>
						</button>
					</view>
					<view class="cu-list menu" :class="[menuBorder?'sm-border':'',menuCard?'card-menu margin-top':'']">
						<view class="cu-item" :class="menuArrow?'arrow':''">
							<button class="cu-btn content" open-type="contact">
								<navigator url="index">
									<text class="cuIcon-btn text-olive"></text>
									<text class="text-grey">Open-type 按钮</text>
								</navigator>
							</button>
						</view>
						<view class="cu-list menu" :class="[menuBorder?'sm-border':'',menuCard?'card-menu margin-top':'']">
							<view class="cu-item" :class="menuArrow?'arrow':''">
								<button class="cu-btn content" open-type="contact">
									<navigator url="index">
										<text class="cuIcon-discoverfill text-orange"></text>
										<text class="text-grey">Navigator 跳转</text>
									</navigator>
								</button>
							</view>
							<view class="cu-list menu" :class="[menuBorder?'sm-border':'',menuCard?'card-menu margin-top':'']">
								<view class="cu-item" :class="menuArrow?'arrow':''">
									<button class="cu-btn content" open-type="contact">
										<navigator url="index">
											<text class="cuIcon-emojiflashfill text-pink"></text>
											<text class="text-grey">头像组</text>
										</navigator>
									</button>
								</view>
							</view>
						</view>

						<view class="bar-bottom cu-bar tabbar bg-white">
							<view class="action">
								<view class="cuIcon-cu-image">
									<image src="/static/tabbar/basics_cur.png"></image>
								</view>
								<view class="text-green">元素</view>
							</view>
							<view class="action">
								<view class="cuIcon-cu-image">
									<image src="/static/tabbar/component.png"></image>
								</view>
								<view class="text-gray">组件</view>
							</view>
							<view class="action">
								<view class="cuIcon-cu-image">
									<image src="/static/tabbar/plugin.png"></image>
									<view class="cu-tag badge">99</view>
								</view>
								<view class="text-gray">扩展</view>
							</view>
							<view class="action">
								<view class="cuIcon-cu-image">
									<image src="/static/tabbar/about.png"></image>
									<view class="cu-tag badge"></view>
								</view>
								<view class="text-gray">关于</view>
							</view>
						</view>

					</view>

				</view>
			</view>
		</view>
	</view>
</template>

<style>
	.body.show {
		overflow: hidden;
		height: 100%;
	}

	#top-bg {
		background-color: #8cc7b5;
		height: 200px;
		width: 100%;
	}

	.info-all {
		display: flex;
		flex-direction: row;
	}

	#touxiang {
		margin-top: 30px;
		margin-left: 10%;
	}

	.info-details {
		display: flex;
		flex-direction: column;
		margin-top: 7%;
		margin-left: 5%;
	}

	.application {
		display: flexbox;
		width: 95%;
		margin: auto;
		margin-top: 10%;
	}

	#grid1 {
		border-radius: 10%;
	}

	.uni-margin-wrap {
		width: 690rpx;
		margin: 0 30rpx;
	}

	.swiper {
		height: 300rpx;
	}

	.swiper-item {
		display: block;
		height: 300rpx;
		line-height: 300rpx;
		text-align: center;
	}

	.bar-bottom {
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		z-index: 1000;
	}

	#ch_font {
		font-family: "微软雅黑", "黑体", "宋体";
	}

	#en_font {
		font-family: Arial, Helvetica, sans-serif;
		font-weight: bold;
		font-style: oblique;
	}
</style>


<script>
	import helper from '../../common/helper.js';
	var corpid = 'wx9320d37828f8e3fc';
	var secrete = 'Y_JZZKnBrp3hiUoUC-SA7hBU0C-IvS1abbys2QuA3w0';
	var usrCode;
	var num;
	var usrName;
	export default {
		data() {
			return {
				portrait: '',
				num: num,
				usrName: usrName,
				indicatorDots: true,
				autoplay: true,
				interval: 2000,
				duration: 500,
				imgList: [{
					url: 'https://image.baidu.com/search/detail?ct=503316480&z=0&ipn=d&word=公司图片&step_word=&hs=2&pn=23&spn=0&di=89760&pi=0&rn=1&tn=baiduimagedetail&is=0%2C0&istype=0&ie=utf-8&oe=utf-8&in=&cl=2&lm=-1&st=undefined&cs=209265447%2C2361612875&os=1790644156%2C1947458261&simid=3347103752%2C315426372&adpicid=0&lpn=0&ln=1017&fr=&fmq=1596094247376_R&fm=&ic=undefined&s=undefined&hd=undefined&latest=undefined&copyright=undefined&se=&sme=&tab=0&width=undefined&height=undefined&face=undefined&ist=&jit=&cg=&bdtype=0&oriquery=&objurl=http%3A%2F%2Fimg2017.cn5135.com%2Fuploads%2Fpic%2F2017%2F5%2F14%2F627430_2017514192521184.jpg&fromurl=ippr_z2C%24qAzdH3FAzdH3Fooo_z%26e3Bvgc8nc_z%26e3Bv54AzdH3FOuuj6AzdH3FSi5oBt2I4w2j-d0l8mnna_z%26e3Bip4s&gsm=18&rpstart=0&rpnum=0&islist=&querylist=&force=undefined'
				}, {
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg'
				}, {
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big25011.jpg'
				}],
				dotStyle: false,
				menuBorder: false,
				menuArrow: true,
				menuCard: false,
			};
		},
		onLoad: function(options) {
			//usrCode = options.code; //获取用户code
			usrCode = 'options.code';
			let that = this;
			//获取access_token
			uni.request({
				url: 'https://qyapi.weixin.qq.com/cgi-bin/gettoken?corpid=' + corpid + '&corpsecret=' + secrete,
				method: 'GET',
				success: function(res1) {
					helper.token = res1.data.access_token;
				}
			});

			//获取用户userid
			uni.request({
				url: 'https://qyapi.weixin.qq.com/cgi-bin/user/getuserinfo?access_token=' + helper.token + '&code=' + usrCode,
				method: 'GET',
				success: function(res2) {
					helper.usrId = res2.data.UserId;
				}
			});

			//获取用户信息
			uni.request({
				//url: 'https://qyapi.weixin.qq.com/cgi-bin/user/get?access_token=' + token + '&userid=' + usrId,
				url: 'https://qyapi.weixin.qq.com/cgi-bin/user/get?access_token=' + helper.token + '&userid=JinkeZhao',
				method: 'GET',
				success: function(res3) {
					that.$data.num = res3.data.extattr.attrs[0].value; //获取工号
					that.$data.usrName = res3.data.name; //获取员工姓名
					that.$data.portrait = res3.data.avatar; //获取微信头像
					console.log(that.$data.portrait);
					console.log(that.$data.num);
					console.log(that.$data.usrName);
				}
			});
			this.TowerSwiper('imgList');
		},

		methods: {

			DotStyle(e) {
				this.dotStyle = e.detail.value
			},
			// cardSwiper
			cardSwiper(e) {
				this.cardCur = e.detail.current
			},
			TowerSwiper(name) {
				let list = this[name];
				for (let i = 0; i < list.length; i++) {
					list[i].zIndex = parseInt(list.length / 2) + 1 - Math.abs(i - parseInt(list.length / 2))
					list[i].mLeft = i - parseInt(list.length / 2)
				}
				this.imgList = list
			},

		}
	}
</script>
